export function computeScore(selectedOptions) {
  return selectedOptions.reduce((pre, cur) => {
    const score = Object.assign({}, pre);

    if (cur.option && cur.option.score) {
      score.anxiety += cur.option.score.anxiety;
      score.avoid += cur.option.score.avoid;
    }

    return score;
  }, {
    anxiety: 0,
    avoid: 0,
  });
}

export function getResult(selectedOptions) {
  const score = computeScore(selectedOptions);
  let matched;

  for (let i = 0; i < results.length; i++) {
    const anxiety = results[i].score.anxiety;
    const avoid = results[i].score.avoid;

    if (anxiety.min <= score.anxiety &&
      anxiety.max >= score.anxiety &&
      avoid.min <= score.avoid &&
      avoid.max >= score.avoid) {
      matched = results[i];
      break;
    }
  }

  return matched;
}



